.inner-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.block {
  border: 1px solid black;
  display: flex;
  justify-content: space-between;
}

.block--h150 {
  height: 150px;
}

.block--h250 {
  height: 250px;
  border-top: none;
}

.col {
  border: 1px solid black;
  height: 100%;
}

.col--full {
  flex: 1;
}

.col--small {
  width: 150px;
}

.col--60 {
  width: 60%;
}

.col--40 {
  width: 40%;
}

.left,
.right {
  height: 100%;
  width: 50%;
  border: 1px solid black;
}

.row {
  display: flex;
  max-height: 100%;
}

.row--h20 {
  height: 20%;
}

.row--h80 {
  height: 80%;
}

/*base styles*/

* {
  box-sizing: border-box;
  /*transition: .5s;*/
}

html,
body {
  padding: 0;
  margin: 0;
  height: 100%;
  width: 100%;
  font-family: 'Lato';
  font-size: 14px;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
  color: #fff;
}

.wrapper {
  position: relative;
  min-height: 100%;
  width: 100%;
  overflow: hidden;
  color: black;
}

.container {
  width: 1024px;
  max-width: 100%;
  height: 100%;
  margin: 0 auto;
}

/*base end*/

@media screen and (max-width: 768px) {
  .col--small.tablet {
    display: none;
  }

  .block--h250 {
    flex-direction: column;
  }

  .left,
  .right {
    width: 100%;
  }
}