/*base styles*/


    *{
        box-sizing: border-box;
        /*transition: .5s;*/
    }

    html, body{
        padding: 0;
        margin: 0;
        height: 100%;
        width: 100%;
        font-family: 'Lato';
        font-size: 14px;

    }

    ul{
        margin: 0;
        padding: 0;
        list-style: none;
    }

    a{
        text-decoration: none;
        color: #fff;
    }

    .wrapper{
        position: relative;
        min-height: 100%;
        width: 100%;
        overflow: hidden;
        color: black;
    }

    .container{
        width: 1024px;
        max-width: 100%;
        height: 100%;
        margin: 0 auto;
    }


/*base end*/
