.inner-container{
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 100%;
}

.block{
	border: 1px solid black;
	display: flex;
	justify-content: space-between;

	&--h150{
		height: 150px;
	}

	&--h250{
		height: 250px;
		border-top: none;
	}
}

.col{
	border: 1px solid black;
	height: 100%;

	&--full{
		flex: 1;
	}

	&--small{
		width: 150px;
	}

	&--60{
		width: 60%;
	}

	&--40{
		width: 40%;
	}
}

.left,.right{
	height: 100%;
	width: 50%;
	border: 1px solid black;
}

.row{
	display: flex;
	max-height: 100%;

	&--h20{
		height: 20%;
	}

	&--h80{
		height: 80%;
	}
}


@media screen and (max-width: 768px){
	.col{
		&--small{
			&.tablet{
				display: none;
			}
		}
	}

	.block{
		&--h250{
			flex-direction: column;
		}
	}

	.left,.right{
		width: 100%;
	}
}